.TransactionItem {
	width: auto;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
	        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
	padding:20px;
	margin-top: 5px; 
    outline: 0;
    font-weight: inherit;
    font-style: inherit;
    font-size: 100%;
    font-family: inherit;
    display: -ms-flexbox;
    display: flex;
    vertical-align: baseline;
    background: transparent;
}

.TransactionItem-image {
	width: 20%;
	box-flex: 0;
    -ms-flex: 0 0 50px;
	    flex: 0 0 50px;
    padding: 0 10px;
    text-align: right;
}

.TransactionItem-image img{
	border-radius: 37.5px;
}


.TransactionItem-Details {
	box-flex: 1;
    -ms-flex: 1 1;
	    flex: 1 1;
}



