/* Add a black background color to the top navigation */
.topnav {
    height:50px;
    background-color: #fff;
    -webkit-box-shadow: 0 0.15rem 0.25rem 0 rgba(0,0,0,.1);
            box-shadow: 0 0.15rem 0.25rem 0 rgba(0,0,0,.1);
    font-family: sailec,-apple-system,BlinkMacSystemFont,avenir next,avenir,helvetica neue,helvetica,ubuntu,roboto,noto,segoe ui,arial,sans-serif;
    font-weight: 300;
    display: block;
}


.topnav span:hover {
    color: #0091ff!important;
}

/* Style the links inside the navigation bar */
.topnav a {
    float: left;
    color: #5b5f66!important;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 14px;

}

.topnav a:hover {
    color: #0091ff!important;
}

.logo {
    color:red;
    font-family: "Arial Rounded MT Bold", "Helvetica Rounded", Arial, sans-serif;
    font-size: 18px;
}

.nav-right {
    padding-right: 15px;
    float: right; 
}





